import React, { useState } from "react"
import styled, { css, keyframes } from "styled-components"
import { colors, styles } from "../consts"
import { Transition } from "react-transition-group"

const TRANSITION_DUR = 600

const ItemHeader = styled.h3`
  white-space: nowrap;
  margin-bottom: 3%;
`

const UnCollapse = keyframes`
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
`

const TimelineItemWrapper = styled.div`
  background-color: ${colors.greyBackground};
  width: 500px;
  grid-row: span 2;
  align-content: middle;
  border-radius: 10px;
  margin: 14px 0;
  padding: 12px 14px;
  box-shadow: ${styles.boxShadow};
  transition: filter ${TRANSITION_DUR /
    1.5}ms, transform ${TRANSITION_DUR}ms, max-height ${TRANSITION_DUR}ms;
  max-height: 500px;
  overflow: hidden;

  :nth-child(odd) {
    justify-self: flex-end;
  }
  ::before {
    content: "";
    position: absolute;
    display: block;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    ${({ side }) =>
      side === "left"
        ? css`
            left: calc(600px - 16px);
          `
        : css`
            right: calc(600px - 16px);
          `}
    box-shadow: ${styles.softBoxShadow};
    transform: translateY(calc(50% + 16px + 7px)), scale(1);
    transition: filter ${TRANSITION_DUR}ms, transform ${TRANSITION_DUR}ms;
    background-color: #4a148c;
  }
  ${({ state }) =>
    state === "exiting" &&
    css`
      filter: blur(5px);
      max-height: 0;
      ::before {
        transform: scale(0.1);
      }
    `}
  ${({ state }) =>
    state === "exited" &&
    css`
      max-height: 0;
      ::before {
        transform: scale(0.1);
      }
    `}
  ${({ state }) =>
    state === "entering" &&
    css`
      animation: ${UnCollapse} ${TRANSITION_DUR}ms;
    `}
  ${({ state }) =>
    state === "entered" &&
    css`
      ::before {
        animation: ${UnCollapse} ${TRANSITION_DUR}ms;
      }
    `}
`
const SWAP_TIME = "0.4s"

const FeatList = styled.ul`
  overflow: hidden;
  margin: 0;
  transition: max-height ${SWAP_TIME}, visibility ${SWAP_TIME},
    opacity ${SWAP_TIME};
  ${({ visible }) =>
    visible
      ? css`
          max-height: 500px;
          visibility: visible;
          opacity: 1;
        `
      : css`
          max-height: 0px;
          visibility: hidden;
          opacity: 0;
        `}
`
const Brief = styled.div`
  transition: visibility ${SWAP_TIME}, opacity ${SWAP_TIME},
    max-height ${SWAP_TIME};
  margin: 0;
  overflow: hidden;
  ${({ visible }) =>
    visible
      ? css`
          visibility: visible;
          opacity: 1;
          max-height: 100px;
        `
      : css`
          visibility: hidden;
          opacity: 0;
          max-height: 0px;
        `}
`
const Feat = styled.li``

const SectionTitle = styled.h4`
  margin-bottom: 0;
`

function JobContent({ position, org, brief, feats, showDetails }) {
  return (
    <>
      <ItemHeader>{`${position} @ ${org}`}</ItemHeader>
      <Brief visible={!showDetails}>{brief}</Brief>
      <FeatList visible={showDetails}>
        {feats.map(feat => (
          <Feat key={feat}>{feat}</Feat>
        ))}
      </FeatList>
    </>
  )
}

function SchoolContent({
  event,
  institution,
  time,
  favorite_course,
  notable_courses,
  showDetails,
}) {
  return (
    <>
      <ItemHeader>{`${event} @ ${institution}`}</ItemHeader>
      <Brief visible={!notable_courses || !showDetails}>
        <SectionTitle>Favorite Course:</SectionTitle>
        {`${favorite_course.title}`}
      </Brief>
      {notable_courses && (
        <FeatList visible={showDetails}>
          <SectionTitle> Notable Courses:</SectionTitle>
          {notable_courses.map(({ code, title }) => (
            <Feat key={code}>
              <strong>{`${code}: `}</strong>
              {title}
            </Feat>
          ))}
        </FeatList>
      )}
    </>
  )
}

function TimelineItem({ visible, type, side, ...rest }) {
  const [showDetails, toggleDetails] = useState(false)
  return (
    <Transition
      in={visible}
      mountOnEnter
      unmountOnExit
      timeout={TRANSITION_DUR}
    >
      {state => (
        <TimelineItemWrapper
          state={state}
          side={side}
          onClick={() => toggleDetails(!showDetails)}
        >
          {type === "job" ? (
            <JobContent showDetails={showDetails} {...rest} />
          ) : (
            <SchoolContent showDetails={showDetails} {...rest} />
          )}
        </TimelineItemWrapper>
      )}
    </Transition>
  )
}

export default TimelineItem
