import { graphql } from "gatsby"

const colors = {
  greyBackground: "#e0e0e0",
  secondaryText: "#455a64",
}

const styles = {
  boxShadow: "0px 0px 16px 2px rgba(51, 51, 51, 0.48)",
  softBoxShadow: "0px 0px 16px 2px rgba(51,51,51,0.38)",
}

export const allEventAttrs = graphql`
  fragment allEventAttrs on TimelineJson {
    type
    event
    institution
    time
    notable_courses {
      code
      title
    }
    favorite_course {
      code
      title
    }
    position
    org
    brief
    feats
  }
`

export { colors, styles }
