import React, { useState } from "react"
import styled, { keyframes } from "styled-components"
import { useStaticQuery, graphql } from "gatsby"
import TimelineItem from "./TimelineItem"
import { GoInfo } from "react-icons/go"
import { colors, styles } from "../consts"
import FilterButtons from "./FilterButtons"

const TimelineHeader = styled.h1`
  text-align: center;
`

const TimelineWrapper = styled.div`
  display: grid;
  position: relative;
  grid-template-columns: 1fr;
  align-items: center;
  width: 1200px;
  margin: auto;
  ::before {
    content: "";
    background: #455a64;
    width: 5px;
    top: 0;
    bottom: 0;
    position: absolute;
    left: 50%;
    border-radius: 6px;
    transform: translateX(-50%);
  }
`

const OFF_SCREEN_POS = "-700px"

const slideIn = keyframes`

  0% {
    right: ${OFF_SCREEN_POS};
  }
  10% {
    right: 0;
  }
  90% {
    right: 0;
  }
  100% {
    right: ${OFF_SCREEN_POS};
  }
`
const HelperText = styled.h4`
  position: absolute;
  top: 6%;
  right: ${OFF_SCREEN_POS};
  color: #546e7a;
  align-items: center;
  display: flex;
  font-size: 1.2rem;
  background-color: ${colors.greyBackground};
  padding: 6px;
  border-radius: 6px;
  animation: ${slideIn} 6s ease-out 10s;
  animation-fill-mode: forwards;
  box-shadow: ${styles.softBoxShadow};
`

function HelperNotification() {
  return (
    <HelperText>
      <StyledGoInfo />
      Click an item for more info!
    </HelperText>
  )
}

const StyledGoInfo = styled(GoInfo)`
  margin-right: 0.3rem;
`

function Timeline() {
  const [filters, updateFilters] = useState(new Set())
  const {
    allTimelineJson: { edges },
  } = useStaticQuery(
    graphql`
      query {
        allTimelineJson {
          edges {
            node {
              ...allEventAttrs
            }
          }
        }
      }
    `
  )

  const toggleFilter = type => {
    filters.delete(type)
      ? updateFilters(new Set(filters))
      : updateFilters(new Set(filters.add(type)))
  }

  return (
    <>
      <div>
        <FilterButtons toggleFilter={toggleFilter} activeFilters={filters} />
        <TimelineHeader>Here's All the Stuff I've Done!</TimelineHeader>
      </div>
      <TimelineWrapper>
        <HelperNotification />
        {edges.map(({ node }, i) => (
          <TimelineItem
            key={`${node.type}-${i}`}
            side={i % 2 == 0 ? "left" : "right"}
            visible={!filters.size || filters.has(node.type)}
            {...node}
          />
        ))}
      </TimelineWrapper>
    </>
  )
}

export default Timeline
