import React from "react"
import styled, { css } from "styled-components"
import { useStaticQuery, graphql } from "gatsby"
import TimelineItem from "./TimelineItem"
import { GoInfo } from "react-icons/go"
import { colors, styles } from "../consts"

const FilterButtonWrapper = styled.div`
  box-shadow: ${styles.boxShadow};
  background-color: ${colors.greyBackground};
  border-radius: 4px;
  width: fit-content;
  text-align: center;
  position: absolute;
  margin-right: 14%;
  right: 0;
`
const LabelText = styled.div`
  padding: 4px 0;
`
const ButtonGroup = styled.div`
  margin: -1px;
`
const Button = styled.button`
  display: inline-block;
  border: none;
  padding: 4px;
  margin: 0;
  text-decoration: none;
  color: ${colors.greyBackground};
  background-color: #607d8b;

  font-size: 1rem;
  cursor: pointer;
  text-align: center;
  transition: filter 160ms ease-out, background-color 500ms ease-out;
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: none;

  :first-child {
    border-radius: 0 0 0 4px;
  }
  :last-child {
    border-radius: 0 0 4px 0;
  }
  :not(:first-child) {
    border-left: 0.2px solid ${colors.greyBackground};
  }

  :hover {
    filter: blur(0.4px);
  }

  ${({ active }) =>
    active
      ? css`
          background-color: #37474f;
        `
      : ""}
`

function FilterButtons({ toggleFilter, activeFilters }) {
  return (
    <FilterButtonWrapper>
      <LabelText>Filter Timeline</LabelText>
      <ButtonGroup>
        <Button
          onClick={() => toggleFilter("job")}
          active={activeFilters.has("job")}
        >
          Job
        </Button>
        <Button
          onClick={() => toggleFilter("education")}
          active={activeFilters.has("education")}
        >
          Education
        </Button>
      </ButtonGroup>
    </FilterButtonWrapper>
  )
}

export default FilterButtons
